.App-viewer {
	flex-grow: 1;
	padding: 15px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	gap: 15px;
	transition: all 0.5s ease;
	overflow: overlay;
}
.view_char_wrapper {
	width: 100%;
	display: flex;
	flex-direction: column-reverse;
}

.wrapper {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	height: 100%;
	overflow: overlay;
	position: relative;

	.header {
		display: flex;
		height: 50px;
		padding: 15px;
		text-align: center;
		font-size: 1.6rem;
		position: absolute;
		width: 100%;
		top: 0;
		background-color: var(--background-pri);
		.ml-auto {
			margin-left: auto;
			font-size: 1.25rem;
			input[type="range"] {
				width: 80px;
				padding: 0 8px;
			}
		}
	}
}

.view_pinyin_warapper {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	height: 100%;
	overflow: overlay;
	.hanzi_ {
		flex-grow: 1;
		display: flex;
		flex-direction: column-reverse;
		gap: 32px;
	}
	.btns {
		border-top: 2px solid var(--border-color);
		padding: 8px 16px;
		display: flex;
		align-items: center;
		.ml-auto {
			margin-left: auto;
			font-size: 1.25rem;
			input[type="range"] {
				width: 80px;
				padding: 0 8px;
			}
		}
	}
	.text-wrapper {
		flex-grow: 1;
		padding: 16px 16px 16px 32px;
		.char_wrapper {
			position: relative;
			display: inline-flex;
			flex-direction: column;
			padding: 0 3px;
			font-size: var(--hanzi_size);
		}
		.char_pinyin {
			line-height: 1.5;
			font-size: var(--hanzi_size);
			color: var(--LimeGreen);
			user-select: none;
			cursor: default;
		}
		.char_hanzi {
			cursor: default;
		}
	}
}
.main {
	display: flex;
	flex-direction: column;
	gap: 32px;
	height: 100%;
	padding: 15px 15px 0 15px;
	background-color: var(--background-pri);
	// overflow: overlay;
	.flex-row {
		height: 100%;
		display: flex;
		gap: 32px;
	}
	.main-content {
		padding: 15px;
		flex-grow: 1;
		height: 100%;
		overflow: overlay;
		user-select: none;
	}
}

.list-wrapper {
	--Hanzi_FontSize: 1.6rem;
	display: flex;
	flex-wrap: wrap;
	gap: 16px;
	padding: 16px;
	height: calc(100% - 50px);
	overflow: overlay;
	.list-item {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		border: 1px solid var(--border-color);
		border-radius: 6px;
		text-align: center;
		overflow: hidden;
		.hanzi {
			cursor: pointer;
			color: var(--LimeGreen);
			font-size: var(--Hanzi_FontSize);
		}
		.pinyin {
			font-size: 1.25rem;
		}
		.description {
			font-size: 1.25rem;
		}
		.item-content {
			flex-grow: 1;
			width: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
		}
		&:hover .card-btns {
			transform: translateY(0);
		}
		.card-btns {
			transition: transform 250ms ease;
			transform: translateY(100%);
			margin-top: auto;
			padding: 0 16px;
			width: 100%;
			display: flex;
			.btns {
				margin-left: auto;
			}

			button {
				-webkit-appearance: none;
				appearance: none;
				background-color: transparent;
				font-size: 1rem;
				padding: 3px 6px;
				border-radius: 2px;
				border: 1px solid var(--border-color);
				color: var(--text-color);
				transition: color 250ms linear;
				cursor: pointer;
				&:hover {
					color: var(--LimeGreen);
				}
			}
		}
	}
}

.btns {
	display: flex;
	gap: 8px;
	align-items: center;
	padding: 0px 12px;
	border-top: 2px solid var(--border-color);
	.slide-input {
		display: flex;
	}
	button {
		-webkit-appearance: none;
		appearance: none;
		background-color: transparent;
		font-size: 1rem;
		padding: 3px 6px;
		border-radius: 3px;
		border: 1px solid var(--border-color);
		color: var(--text-color);
		transition: color 250ms linear;
		cursor: pointer;
		&:hover {
			color: var(--LimeGreen);
		}
	}
}

.search-wrapper {
	margin: 0 auto;
	form {
		display: flex;
		gap: 8px;
		align-items: center;
	    font-size: 1rem;
		.input-wrapper{
			display: flex; gap: 8px;
			align-items: center;
			input[type="search"] {
			height: 45px;
			flex-grow: 1;
			background-color: transparent;
			padding: 8px 16px;
			border: none;
			transition: all 250ms ease;
			outline: none;
			color: inherit;
			&:focus::placeholder {
				color: var(--LimeGreen);
			}
		}
		button {
			display: inline-block;
			height: 50px;
			padding: 8px 16px;
			-webkit-appearance: none;
			appearance: none;
			background-color: transparent;
			color: var(--text-color);
			transition: color 250ms linear;
			outline: none;
			cursor: pointer;
			&:focus {
				color: var(--LimeGreen);
			}
			
		}
		}
			select{
				-webkit-appearance: none;
				appearance: none;
				background-color: transparent;
				border: 1px solid var(--border-color);
				border-radius: 3px;
				padding: 3px 6px;
				color: inherit;
				text-align: center;
				option{background-color: var(--background-sec)!important;}
			}
		
	}
}


.test-wrapper{
	flex-grow: 1;
	display: flex;
	justify-content: center;
	align-items: center;
}
.test-container{
	position: relative;
	display: flex;
	flex-direction: column;
	width: 50%;
	height: calc(100% / 2)px;
	padding: 45px 15px;
	border-radius: 6px;
	border: 2px solid var(--border-color);
	background-color: var(--background-sec);
	&::before{
		position: absolute;
		left: 25px;
		bottom: 10px;
		font-size: 0.75rem;
		content:'progress : ' attr(data-progress) ' %';
	}
	&::after{
		position: absolute;
		bottom: 10px;
		right: 25px;
		font-size: 0.75rem;
		color: inherit;
		content: attr(data-match);
	}
	h3{
		flex-grow: 1;
		text-align: center;
		font-size: 1.5rem;
	}
	button[type="reset"]{
		    margin-top: 16px;
			width: 100%;
			padding: 8px;
			background-color: var(--BrightRed);
			outline: none;
			border: none;
			color: var(--text-color);
			border-radius: 6px;
			opacity: 0.8;
			&:hover{
				opacity: 1;
			}
			&:focus{
				opacity: 1;
				outline: 1px solid var(--BrightRed);
			}	}
	.test-Question{
		text-align: center;
		font-size: 1.5rem;
		line-height: 1.6;
		padding: 8px 0;
	}
	.test-Answer{
		flex-grow: 1;
		background-color: var(--background-pri);
		form{
			display: flex;
			gap: 8px;
			align-items: center;
		}
		input{
			flex-grow: 1;
			height: 35px;
			background-color: transparent;
			font-size: 1.25rem;
			padding: 8px 16px;
			outline: none;
			border: none;
			border-bottom: 2px solid var(--border-color);
			transition: border 250ms ease;
			color: var(--text-color);
			&:focus{
				border-bottom-color: var(--LimeGreen);
			}
		}
		button[type="submit"]{
			padding: 8px;
			background-color: transparent;
			outline: none;
			border: none;
			color: var(--text-color);
			width: 35px;
			height: 35px;
			cursor: pointer;
			&:hover{
				filter: brightness(1.25);
			}
			&:focus{
				outline: 1px solid var(--LimeGreen);
			}	
	}
	}
}